import { Button , ButtonGroup , styled } from "@mui/material"
import { useState } from "react"


const Buttonincdec = () => {
    const [count , setcount] = useState(1);

    const countSub =() => {
        const Sub = count-1;
        if(Sub > 0){
            setcount(Sub)
        }
        
        
    }

    const countAdd =() => {
        const Add = count+1;
        setcount(Add)
    }
const Container = styled(ButtonGroup)`
margin-top:30px ; ` 

const ButtonStyle = styled(Button)`
 
border-radius:50%`
    return(
         <Container>
           <ButtonStyle onClick={countSub}>-</ButtonStyle>
           <Button disabled>{count}</Button>
           <ButtonStyle onClick = {countAdd}>+</ButtonStyle>
          
       </Container>
    )
}

export default Buttonincdec;