import Carousel from "react-multi-carousel";
import { Box, styled, Typography, Button, Divider } from "@mui/material";
import {Link} from 'react-router-dom';
import {useState} from "react"; 

import Countdown from 'react-countdown';


import "react-multi-carousel/lib/styles.css";



const responsive = {

    desktop: {
        breakpoint: { max: 3000, min: 1024 },
        items: 5
    },
    tablet: {
        breakpoint: { max: 1024, min: 464 },
        items: 2
    },
    mobile: {
        breakpoint: { max: 464, min: 0 },
        items: 2
    }
};


const timerURL = 'https://static-assets-web.flixcart.com/www/linchpin/fk-cp-zion/img/timer_a73398.svg';
const Slide = ({ getproduct, title, timer }) => {
    


    const renderer = ({ hours, minutes, seconds }) => {

        var time = <Box variant="span">{hours}:{minutes}:{seconds} left</Box>; 
         
        return time
    }



    const Wrapper = styled(Box)`
     background:#fff ;
     margin-top:20px
     
     `


    const DTwrapper = styled(Box)`
     display:flex;
     padding : 15px 25px;
     font-size:22px;
      
     `

    const Deal = styled(Typography)`
     font-size:22px;
     font-weight:600;
     `

    const ViewAllButton = styled(Button)`
     background:blue ; 
     font-size: 16px ;
     margin-left: auto;
     font-weight:600;
     color:#ffffff;
    
     `

    const Text = styled(Typography)`
     font-size:14px ; 
     `

    return (
        <Wrapper>
            <DTwrapper>
                <Deal>  {title} </Deal>
                {
                    timer ?
                        <Box style={{ display: "flex" }}><img src={timerURL} alt="timer logo" style={{ width: 25, marginLeft: 12 }} />

                            <Countdown date={Date.now() + 5.04e+7} renderer={renderer} />
                        </Box>
                        :
                        ""

                }



                <ViewAllButton variant="container">View All</ViewAllButton>
            </DTwrapper>

            <Divider />
            <Carousel responsive={responsive}
                swipeable={false}
                draggable={true}
                infinite={true}
                autoPlay={true}
                autoPlaySpeed={4000}
                keyBoardControl={true}>
                <> </>
                {getproduct &&
                    getproduct.map((data) => (
                        <Link to={`/Product_Detail/${data.id}`} style = {{textDecoration:"none"}}>
                        <Box textAlign="centre" style={{ padding: "25px  15px", alignItem: "centre" }}>

                           <img src={data.url} alt={data.id} style={{ width: "auto", height: 150 }} />
                          <Text style={{ color: "#212121", fontWeight: 600 }}>{data.title.shortTitle}</Text> 
                            <Text style={{ color: "green" }}>{data.discount}</Text>
                            <Text style={{ color: "#212121", opacity: "0.6" }}>{data.tagline}</Text>


                        </Box>
                    </Link>
                    ))
                }
            </Carousel>
        </Wrapper>



    )
}

export default Slide;