import { Box, styled, Typography } from "@mui/material" ;
import { useState } from "react";
import { useEffect } from "react";



const TotalBalance = ({cartItems}) => {

    const [ Price , SetPrice] = useState(0);
    const [Discount , SetDiscount] = useState(0);
    const Heading = styled(Box)`
    padding : 16px 25px ;
    background : #fff ;
    border-bottom : 1px solid #f0f0f0 ; 
    `

    const Container = styled(Box)`
    padding:16px 25px ; 
    background: #fff;

    & > p {
        margin-bottom:20px ;
        font-size : 14px;
    }
    `
    const totalAmount = () => {
        let   Price = 0 ;
        let  Discount = 0 ;
        

        cartItems.map(Item => (
            Price += Item.price.mrp ,
            Discount += (Item.price.mrp - Item.price.cost) 
        ))
        SetPrice(Price) ;
        SetDiscount(Discount);
    }


    useEffect( () => {
        totalAmount()
    } , [cartItems])


    const PriceBox = styled(Box)`
    float : right`
    return(
        <Box style={{marginLeft:10}}> 
            <Heading>
                <Typography style={{color :"#878787"}}>Product Details</Typography>
            </Heading>

            <Container>
                <Typography>Price ({cartItems?.length} item)
                 <PriceBox component="span" > {Price}</PriceBox>
                 </Typography>

                <Typography>Discount
                <PriceBox component="span" >-{Discount}</PriceBox>
                </Typography>

                <Typography> Delivery Charges
                <PriceBox component="span" >40</PriceBox>
                </Typography>

                <Typography> Total Amount
                <PriceBox component="span" >{Price - Discount + 40} </PriceBox>
                </Typography>
                <Typography style={{color:"green" , fontSize:14}}>You Will Save {Discount - 40 }  </Typography>
            </Container>
        </Box>
    )
}

export default TotalBalance