import { Typography, Box, styled } from "@mui/material"

import { navData } from "../../../constants/imgdata"
 const Nav = () => {

    const Navbaox = styled(Box)(({theme}) => ({
        display:"flex" ,
        background:"#fff",
         width:"100%",
         overflow : "hidden" , 
         justifyContent:"space-between" ,
        margin:"55px 150px 0 150px" ,
        textAlign:"center",
        textTransform: "underline" , 
        [theme.breakpoints.down("md")] : {
              margin:"0" ,
              
        }
    }))
    

     
    const Navconbaox = styled(Box)`
    padding:12px 8px;`
    const Navtext = styled(Typography)`
    font-size:14px;`
    return (
<Box style={{background : "#fff" }}>
        <Navbaox>
            
             {    
                 navData.map(data => (
                     
                    <Navconbaox>
                         <img src={data.url} alt="product images" style={{width:64}}></img>
                         <Navtext>{data.text}</Navtext>
                    </Navconbaox>
                     
                ))
                 
            }
        </Navbaox>
        </Box>
    )
}
export default Nav;