import { Grid, styled } from "@mui/material"

import { imageURL } from "../../../constants/imgdata";


const url = 'https://rukminim1.flixcart.com/flap/3006/433/image/4789bc3aefd54494.jpg?q=50';


const Banners = () => {

    const Img = styled("img") ( ( {theme}) => ( {
        marginTop : 20 ,
        justifyContent : "space-between" , 
        width : "100%" , 
    [theme.breakpoints.down("md")] : {
        objectFit : "cover" , 
        height:125 
    }}

     ) )
    return (

     
<>
        <Grid lg={12} sm={12} md={12} xs ={12} container>
            {
                imageURL.map(data => (
                    <Grid  style = {{marginTop:20 , justifyContent: "space-between"}}lg={4} sm={12} md={4} xs ={12} container>
                    <img src={data} alt="Banners "  style={{width:"100%" }}/>
                    </Grid>
                ))
            }


        </Grid>
        <Img src= {url} alt = 'covid19' />
        </>
    )
}

export default Banners;