
import styled from "@emotion/styled";
import { Box, InputBase, Styled, List, ListItem } from "@mui/material"
import SearchIcon from '@mui/icons-material/Search';
import { useState, useEffect } from "react";
import { getproducts } from "../../redux/action/productgetapi";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";

const Searchproductbox = styled(Box)`
background:#ffffff;
margin-left:30px;
width:38%;
border-radius:2px;
display:flex`
const Searchproduct = styled(InputBase)`

font-size:14px;
height:36px;
padding-left:6px;
width:100%;
`
const Searchicon = styled(Box)`
background:#ffffff;
color:blue;
margin:6px 6px 0 0 ;`

const Lists = styled(List)`
position : absolute ; 
background:#ffffff  ;
color : #000;
margin-top: 36px;
`

const Search = () => {
    const dispatch = useDispatch()


    const [searchItem, setserachItem] = useState("")

    const products = useSelector(state => state.getproducts.products.login);

    


    useEffect(() => {
        dispatch(getproducts())
    }, [dispatch])



    const textChange = (text) => {

        setserachItem(text)
         

    }
    return (
        <Searchproductbox>
            <Searchproduct placeholder="Search for product, brand & more " onChange={(event) => textChange(event.target.value)} />
            <Searchicon >
                <SearchIcon />
            </Searchicon >

            {
                searchItem &&
                <Lists>
                    {
                        products.filter(product => product.title.longTitle.toLowerCase().includes(searchItem.toLocaleLowerCase())).map(product => (
                            <ListItem>
                            <Link to={ `/Product_Detail/${product.id}`} onClick = {() => setserachItem("")}  >
                                {product.title.longTitle}
                                
                            </Link>
                            </ListItem>
                        ))
                    }
                </Lists>
            }
            <List>

            </List>
        </Searchproductbox>
    )
}
export default Search;