import { Box, styled } from "@mui/material"
import Slide from "./slides"

const adURL = 'https://rukminim1.flixcart.com/flap/464/708/image/633789f7def60050.jpg?q=70';



const Midslide = ({getproduct , title , timer}) => {
    const Slide1 = styled(Box)(({theme}) => ({
        width : "83%" ,
        [theme.breakpoints.down("md")] : {
            width:"100%"
        }
    }))
    const Adbanner = styled(Box)(({theme}) => ({
        marginTop : 100 ,
        [theme.breakpoints.down("md")] : {
            display : "none"
        }
    }))
    return (
        <Box style={{display:"flex"}}>
            <Slide1>
                <Slide getproduct={getproduct} title={title}  timer={timer}></Slide> 
            </Slide1>
            <Adbanner style={{marginTop:100}}>
                <img src={adURL} alt="ad" style={{width:185}}/>
            </Adbanner>
        </Box>
    )
}

export default Midslide;