import Nav from "./nav"
import Carousles from "./carouslebanner";
import Midslide from "./midslide";
import Banners from "./banners";
import {Box , styled} from "@mui/material"
import { useEffect } from "react";
import { useDispatch , useSelector } from "react-redux";
import { getproducts } from "../../../redux/action/productgetapi";
import Slide from "./slides";
const Home = () => {
    const getproductes = useSelector(state => state.getproducts.products.login)
     
    
    
    const dispatch = useDispatch();
    useEffect( () => {
        
        dispatch(getproducts())
        
},[dispatch])


    const Navbox = styled(Box)`
    border-bottom:6px solid whitesmoke;
    `
   
    return(
        <Box>
            <Navbox> 
                <Nav/>
            </Navbox>
            <Carousles/>
            <Midslide getproduct={getproductes} title="Deal of the Day" timer={true}/>
            <Banners/>
            <Slide getproduct={getproductes} title="Suggested for You " timer={false}/>
            <Slide getproduct={getproductes} title="Accessiories" timer={false}/>
            <Slide getproduct={getproductes} title="Extra 10% Off" timer={false}/>
            <Slide getproduct={getproductes} title="Buy One Get One Free" timer={false}/>
           
        </Box>
    )
}

export default Home;