import { AppBar, Toolbar, Box, styled, Typography, IconButton, Drawer, List, ListItem } from "@mui/material";
import Search from "./search"
import Buttons from "./buttons";
import MenuIcon from '@mui/icons-material/Menu';
import { Link } from "react-router-dom";
import { useState } from "react";





const Header = () => {
    const [open, setopen] = useState(false);

    const logoURL = 'https://static-assets-web.flixcart.com/www/linchpin/fk-cp-zion/img/flipkart-plus_8d85f4.png';
    const subURL = 'https://static-assets-web.flixcart.com/www/linchpin/fk-cp-zion/img/plus_aef861.png';

    const MenuButtons = styled(IconButton)(({ theme }) => ({
        display: "none",
        color: "white",
        fontSize: "inherit",
        [theme.breakpoints.down("md")]: {
            display: "block"
        }
    }))
    const Styleheader = styled(AppBar)`
  height:55px;
`
    const Component = styled(Link)`
margin-left:12%;
line-height:0;
text-decoration:none;
`
    const Line = styled(Typography)`

font-style:italic;
font-size:12px;
`
    const Logoimg = styled("img")({
        width: 12,
        height: 12,
        marginLeft: 4

    })




    const Logobox = styled(Box)`
    display:flex;
`

    const Btnbox = styled(Box)(({ theme }) => ({
        margin: "0 5% 0 auto",
        [theme.breakpoints.down("md")]: {
            display: "none"
        }
    }))

    const handleOpen = () => {
        setopen(true)
    }
    const handleClose = () => {
        setopen(false)
    }

     

    return (

        <Styleheader>
            <Toolbar style={{ height: 55 }}>

                <MenuButtons onClick={handleOpen}>
                    <MenuIcon />
                </MenuButtons>

                <Drawer open={open} onClose={handleClose} >
                    <Box style={{ width:200 }} onClick={handleClose}>
                        <List>
                            <ListItem button>
                                
                                <Buttons />
                            </ListItem>
                        </List>

                    </Box>

                </Drawer>

                <Component to='/'>

                    <img src={logoURL} alt='enter' style={{ width: 75 }} />
                    <Logobox>
                        <Line className="logoimg" style={{ color: "white" }}>Explore <Box component="span" style={{ color: "yellow" }}>plus</Box></Line>

                        <Logoimg src={subURL} alt="logo" />

                    </Logobox>
                </Component>


                <Search />
                <Btnbox>
                    <Buttons />
                </Btnbox>

            </Toolbar>

        </Styleheader>

    )
}
export default Header;