import { Badge, Box, Button, styled, Typography } from "@mui/material";
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import LoginDialog from "./dialog/loginDialog";
 
import { useState , useContext } from "react";
import { Datafetch } from "../../context/DataProviders";
import Profile from "./homeData/Profile";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";



const Buttons = () => {
const {cartItems} = useSelector(state =>  state.cart)
    const [open , setopen] = useState(false);

    const { accounts , setAccounts}= useContext(Datafetch);

    const Container= styled(Box)`
    display:flex;
   

    & > Button , & > p , &> div {
        margin-right:40px;
        display:flex;
        align-items:centre;
        font-size:14px;
        border-radius:2px;
    }

    & > Button {
        background:#fff;
        color:blue;
        padding:5px 40px;
        text-transform:none;
        height:32px;
        box-shadow:none;
        border-radius:2px;

    }
`
    // const Containers = styled(Box)(({ }) => ({
    //     dispaly: "flex",
    //     "& > Button , & > p , &> div" : {
    //         marginRight: 40,
    //         display: "block",
    //         alignItems: "centre",
    //         fontSize: 14,
    //         borderRadius: 2,
    //         color:"green"
    //     },

    //     " & > Button "  : {
            
    //         background: "#fff",
    //         color: "blue",
    //         padding: "5px 40px",
    //         textTransform: "none",
    //         height: 32,
    //         boxShadow: "none",
    //         borderRadius: 2,
            
    //     }



    // }))
    

    const openDialog = () => {
        setopen(true)
    } 
    return (
        <Container>
            {accounts ? <Profile accounts={accounts} setAccount={setAccounts} />:
             <Button variant="contained" onClick={() => openDialog()}>Login</Button>}
            <Typography style={{width:120 , marginTop:3}}  >Become a seller </Typography>
            <Typography style={{wdth:70 , marginTop:3}}>More</Typography>
            
            <Box>
                
            <Link to ="Add_To_Carts" style = {{textDecoration:"none"  , color :"#fff" ,  display : "flex"}}> 
            <Badge badgeContent={cartItems?.length} color="secondary" >
               <ShoppingCartIcon />

            </Badge>

                <Typography style={{marginTop:3 }}>Cart </Typography>

                </Link>
            </Box>
            
            <LoginDialog open = {open} setopen = {setopen}/>
             

        </Container>


    )
}

export default Buttons;