
import { Box, Grid, Typography, styled, Button } from "@mui/material";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import Cartitem from "./Cartitm";
import TotalBalance from "./TotalBalance";
import EmptyCart from "./Emptycart";
const Cart = () => {
    const { cartItems } = useSelector(state => state.cart);

    const Container = styled(Grid)(({theme}) => ({
        padding : "30px 130px" , 
        [theme.breakpoints.down("md")] : {
padding : 0
        }
    }))
    
     
     
    

    const Btnplaceorder = styled(Button)`
    text-formation : none ;
    display : flex ; 
    background:#fb641b;
    color:#fff;
    height:40px;
    text-align:centre;
    width:250px;
    margin-left:auto;
    border-radius:2px;
    
    `
    const ContainerBtn = styled(Box)`
    padding: 16px 22px;
    box-shadow = 0 -2px 10px 0 rgb(0 0 0 / 10%);
    border-top: 3px solid #f0f0f0;
    background : #fff ;
    `

    return (
        <>
            {
                cartItems.length ? <Container container >
                    <Grid lg={9} md={9} sm={12} xs={12}>

                        <Box style={{ background: "#fff", padding: 15, border: "3px solid #f0f0f0" }}><Typography >My Cart ({cartItems.length})</Typography></Box>
                        <Box >
                            {
                                cartItems.map(item => (
                                    <Cartitem item={item} />

                                ))
                            }
                        </Box>
                        <Link to="/Place-order" style={{textDecoration:"none"}}>
                        <ContainerBtn>
                        <Btnplaceorder>Place Order</Btnplaceorder>
                        </ContainerBtn>
                        </Link>
                    </Grid>
                    
                    <Grid item lg={3} md={3} sm={12} xs={12}>

                        <TotalBalance cartItems= {cartItems}/>

                    </Grid>

                </Container> :
                <Box style={{marginTop : "90px"}} >
                 <EmptyCart/>
                 </Box>
            }
        </>
    )
}

export default Cart; 