import axios from "axios";


const URL = "https://fair-blue-bear-hat.cyclic.app" ;
export const authenticateSignup = async (userDataSignup) => {
    
    try{
      return await axios.post(`${URL}/signup`, userDataSignup);
      }catch(error){
         return  error.message
      }
    
}

export const  authenticateLogin = async (userDataLogin) => {
   try{
   return await axios.post(`${URL}/login`, userDataLogin);
   }catch(error){
      console.log("error while finding",error.message)
   }
}

export const PaymentUsingPaytm = async(data) => {
   try {
      return  await axios.post(`${URL}/payment`, data) ; 
   }catch (error){

      console.log("Error while payment using paytm " , error.message)
       
   }
}

