import { Box, Button, styled } from "@mui/material"
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
import FlashOnIcon from '@mui/icons-material/FlashOn';
import { Link, useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { post } from "../paytm_utils/paytm";
 
import { AddtoCart } from "../../redux/action/CartAction";
import { useState } from "react";
import { PaymentUsingPaytm } from "../../service/api";

const Leftcomponent = styled(Box)`
padding:40px 0  0 80px ; 
min-width :40%;
`
const Leftcomponentimg = styled("img")({
    padding: "15px",
    border: "4px solid whitesmoke",
    width: "90%"
})

const ButtonStyle = styled(Button)(({ theme }) => ({

width:"46%",
height: "50px",
[theme.breakpoints.down("md")] : {
    fontSize:"14px",
    height: "90px" , 
     
    
    marginRight:"20px"
}
}))


const ActionItem = ({ product }) => {
    const Navigate = useNavigate();
    const [quantity , setquantity]  = useState(1)
    const dispatch = useDispatch();
    const {id} = product;


    const BuyNow = async() => {
        let response = await PaymentUsingPaytm({ammount : 500 , email_id : "panditji123@gmail.com"}) ; 
        let information = {
            action : "https://securegw.stage.paytm.in/order/process" ,
            params : response 
        }
        post(information)
        
    }
    

    

    const addCart = () => {
        dispatch(AddtoCart(id , quantity))
        Navigate("/Add_To_Carts")
    }
    return (
        <Leftcomponent >
            <Box style={{ padding: "15px  20px" }}>
                < Leftcomponentimg src={product.detailUrl} alt="detail-url" />
            </Box>
<Box style={{display : "flex"}} >
            <ButtonStyle variant='contained' style={{ marginRight: 10, background: "#ff9f00" }} onClick={addCart}><AddShoppingCartIcon /> Add To Cart</ButtonStyle>
            <ButtonStyle variant='contained' style={{ background: "#fb541b" }} onClick={BuyNow}><Link to ="place-order" style={{textDecoration : "none" , color : "white" , display : "flex"}}> <FlashOnIcon />Buy Now </Link></ButtonStyle>
        </Box>
        </Leftcomponent>
    )
}

export default ActionItem;