import { Typography , Box, styled, Button} from "@mui/material";
import {Link} from "react-router-dom"
 



const EmptyCart = () => {
    const Component = styled(Box)`
    
    width:80%;
    height:70vh;
    background:#fff;
    margin:auto ;`

    const Container = styled(Box)`
    padding:80px ; 
    text-align: center ;`
    const imgurl = 'https://rukminim1.flixcart.com/www/800/800/promos/16/05/2019/d438a32e-765a-4d8b-b4a6-520b560971e8.png?q=90';

    return(
        <Component>
        < Container>
            <img src={imgurl} alt="empty-cart" style={{width:"15%" }}/>
            <Typography style={{fontSize : 14 , margin :"10px 0  10px 0"}}>Your Cart Is Empty</Typography>
            <Typography style={{fontSize : 14 , margin :"10px 0  10px 0"}}>Please Add Item Into Your Cart </Typography>
            <Link to = "/" style = {{textDecoration:"none"}}><Button >Continue To Shopping</Button></Link>
        </ Container>
        </Component>
        
    )
}

export default EmptyCart;