import { Box, Typography, styled, Button } from "@mui/material";
import { useDispatch } from "react-redux";
import { RemoveFromCart } from "../../redux/action/CartAction";
import { fassured } from "../productPage/ProductDetail";
import Buttonincdec from "./Buttonincdec";

const Cartitem = ({ item }) => {

    const dispatch = useDispatch()

    const RemoveItem = (id) => {
        dispatch(RemoveFromCart(id))
    }

    const Container = styled(Box)`
       
      border : 3px solid #f0f0f0;
      display:flex;
      background : #fff;
       
    `

    const Leftcomponent = styled(Box)`
      margin:20px;
      display : flex ;
      flex-direction:column;
    `
    const TypoSeller = styled(Typography)`
         font-size:14 ;
         color : #878787;
         margin-top:10px;
         `

    return (
        <Container>
            <Leftcomponent>
                <img src={item.url} alt="product" style={{ width: 100, borderWidth: " 10px" }} />
                <Buttonincdec />
            </Leftcomponent>

            <Box>
                <Typography>{item.title.longTitle}</Typography>
                <TypoSeller >Seller : Commnet
                    <Box component='span' ><img src={fassured} alt="fassured" style={{ width: 50, marginLeft: " 10px" }} /> </Box>
                </TypoSeller>

                <Typography style={{ margin: "15px 0" }}>
                    <Box component="span" style={{ fontWeight: 550, marginLeft: 16 }}>{item.price.cost}</Box>
                    <Box component="span" style={{ marginLeft: 16 }}><strike>{item.price.mrp}</strike></Box>
                    <Box component="span" style={{ marginLeft: 16 }}>{item.price.discount} Off</Box>

                </Typography>
                
                    <Button onClick={() => RemoveItem(item.id)} > Remove item</Button>
                
            </Box>
        </Container>
    )
}

export default Cartitem;