import { Typography , Box , Dialog, Button } from "@mui/material"
import { useState } from "react"
import {Link} from "react-router-dom"


const Successful = () => {
    const [open , setopen] = useState(true)
    return(
        <Box> 
            <Box>
                
                <Dialog open={open} >
                    <Box style={{textAlign:"center"}}>
                    <img src= "/image.jpg" style={{width:"100%" , height:"100%", scrollBar:'none' }}/>
                    <Typography style={{fontSize:20 , textAlign:"center" , margin:"15px 0 15px 0 "}}>Thank you! For purchasing product through our website</Typography>
                    <Link to="/" style={{textDecoration : "none"}}>
                    <Button variant="contained" style={{background:"#F7A200" , margin:"15px 0 15px 0 "}} >Done</Button>
                    </Link>
                    </Box>
                </Dialog>
            </Box>
        </Box>
    )
}

export default Successful;